<template>
    <div>
        <div class="text-center mx-auto font-bold">
            <div class="mb-5">
                <h1 class="text-white text-5xl title">To Do List</h1>
            </div>
        </div>
        <div class="cardDivInner w-1/2 text-center mx-auto border border-zinc-500 rounded shadow-lg  font-bold p-5">
            <div class="mt-6">
                <div class="">
                    <input type="text" @keyup.enter="addTodo" class="focus:border-gray-500 focus:ring-0 focus:offset-0 addTodo text-gray-title border border-gray-500 rounded-lg text-left px-3 py-2 drop-shadow-2xl bg-transparent w-full" placeholder="+ Add To List">
                </div>

                <div v-for="item in todos" :key="item.id" class="transition ease-in-out delay-200 duraiton-200">
                    <div id="checklist" @click="toggleTodo" class="text-white transition duration-200 ease-out text-1xl text-left my-5 transition ease-in-out delay-150   duration-200 cursor-pointer flex justify-start align-center">
                        <input v-bind:id="item.id" type="checkbox" name="checkbox"  class="outline-0 offset-0 ring-0 border-0 focus:outline-0 focus:ring-0 focus:offset-0 focus:border-0 focus:ring-offset-0"/>
                        <label v-bind:for="item.id">{{ item.text }}</label>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'App',
        data() {
            return {
                todos: [
                    {
                        id: 1,
                        text: 'Learn Vue',
                        completed: true
                    },
                    {
                        id: 2,
                        text: 'Learn Vuex',
                        completed: false
                    },
                    {
                        id: 3,
                        text: 'Learn Vue Router',
                        completed: false
                    }
                ]
            }
        },
        methods: {
            addTodo(e) {
                this.todos.push({
                    id: this.todos.length + 1,
                    text: e.target.value,
                    completed: false
                })
                e.target.value = ''
                console.log(e.target.value);
            },
            removeTodo(todo) {
                this.todos.splice(this.todos.indexOf(todo), 1)
            },
            toggleTodo(todo) {
                todo.completed = !todo.completed
                console.log(todo.completed);
            }            ,
            removeCompleted() {
                this.todos = this.todos.filter(todo => !todo.completed)
            },
        }
    }
</script>

<style lang="scss"  scoped>
.title{
    font-family: 'Pacifico', cursive;
}
.text-gray-title{
    // color: #868e96;
    color: #a3bdbe;
}
.text-gray-title::placeholder {
    color: #a3bdbe;
}

#checklist {

    --check: #4F29F0;
    --disabled: #C3C8DE;
    --width: 100px;
    --height: 140px;
    --border-radius: 10px;
    position: relative;
    label {
        color: var(--text);
        position: relative;
        display: grid;
        align-items: center;
        transition: color .3s ease;
        &::before,
        &::after {
            content:"";
            position: absolute;
        }
        &::before {
            height: 2px;
            width: 8px;
            left: -27px;
            background: var(--check);
            border-radius: 2px;
            transition: background .3s ease; 
        }
        &:after {
            height: 4px;
            width: 4px;
            top: 8px;
            left: -25px;
            border-radius: 50%;
        }
    }
    input[type="checkbox"] {
        -webkit-appearance: none;
        -moz-appearance: none;
        position: relative;
        margin: 8px 15px 0 0;
        background: var(--background);
        &::before,
        &::after {
            content:"";
            position: absolute;
            height: 2px;
            top: 50%;
            background: var(--check);
            border-radius: 2px;
        }
        &::before {
            width: 0px;
            right: 60%;
            transform-origin: right bottom;
        }
        &::after {
            width: 0px;
            left: 40%;
            transform-origin: left bottom;
        }
        &:checked {
            &::before {
                animation: check-01 .4s ease forwards;
            }
            &::after {
                animation: check-02 .4s ease forwards;
            }
            + label {
                color: var(--disabled);
                animation: move .3s ease .1s forwards;
                &::before {
                    background: var(--disabled);
                    animation: slice .4s ease forwards;
                }
                &::after {
                    animation: firework .5s ease forwards .1s;
                }
            }
        }
    }
}

    @keyframes move {
    50% {
    padding-left: 8px;
    padding-right: 0px;
    }
    100% {
    padding-right: 4px;
    }
    }
    @keyframes slice {
    60% {
    width: 100%;
    left: 4px;
    }
    100% {
    width: 100%;
    left: -2px;
    padding-left: 0;
    }
    }
    @keyframes check-01 {
    0% {
    width: 4px;
    top: auto;
    transform: rotate(0);
    }
    50% {
    width: 0px;
    top: auto;
    transform: rotate(0);
    }
    51% {
    width: 0px;
    top: 4px;
    transform: rotate(45deg);
    }
    100% {
    width: 5px;
    top: 4px;
    transform: rotate(45deg);
    }
    }
    @keyframes check-02 {
    0% {
    width: 4px;
    top: auto;
    transform: rotate(0);
    }
    50% {
    width: 0px;
    top: auto;
    transform: rotate(0);
    }
    51% {
    width: 0px;
    top: 4px;
    transform: rotate(-45deg);
    }
    100% {
    width: 10px;
    top: 4px;
    transform: rotate(-45deg);
    }
    }
    @keyframes firework {
    0% {
    opacity: 1;
    box-shadow: 0 0 0 -2px #4F29F0, 0 0 0 -2px #4F29F0, 0 0 0 -2px #4F29F0, 0 0 0 -2px #4F29F0, 0 0 0 -2px #4F29F0, 0 0 0 -2px #4F29F0;
    }
    30% {
    opacity: 1;
    }
    100% {
    opacity: 0;
    box-shadow: 0 -15px 0 0px #4F29F0, 14px -8px 0 0px #4F29F0, 14px 8px 0 0px #4F29F0, 0 15px 0 0px #4F29F0, -14px 8px 0 0px #4F29F0, -14px -8px 0 0px #4F29F0;
    }
}

</style>